@import "../bourbon/bourbon";

@mixin opacity($amount) {
  opacity: $amount;
  filter: alpha(opacity = $amount * 100); }

$namespace: "growl";

$black: #000;
$white: #FFF;
$small: 200px;
$medium: 250px;
$large: 300px;
$spacing: 10px;

$duration: 0.4s;

.ontop {
  z-index: 50000;
  position: fixed; }

#growls {
  &-default {
    @extend .ontop;
    top: $spacing;
    right: $spacing; }
  &-tl {
    @extend .ontop;
    top: $spacing;
    left: $spacing; }
  &-tr {
    @extend .ontop;
    top: $spacing;
    right: $spacing; }
  &-bl {
    @extend .ontop;
    bottom: $spacing;
    left: $spacing; }
  &-br {
    @extend .ontop;
    bottom: $spacing;
    right: $spacing; }
  &-tc {
    @extend .ontop;
    top: $spacing;
    right: $spacing;
    left: $spacing; }
  &-bc {
    @extend .ontop;
    bottom: $spacing;
    right: $spacing;
    left: $spacing; }
  &-cc {
    @extend .ontop;
    top: 50%;
    left: 50%;
    margin-left: $medium / -2; }
  &-cl {
    @extend .ontop;
    top: 50%;
    left: $spacing; }
  &-cr {
    @extend .ontop;
    top: 50%;
    right: $spacing; }
  &-tc,&-bc {
    .#{$namespace} {
      margin: {
        left:  auto;
        right: auto; } } } }

.#{$namespace} {
  @include opacity(0.8);
  position: relative;
  border-radius: 4px;
  @include transition(all $duration ease-in-out);

  &.#{$namespace}-incoming {
    @include opacity(0.0); }

  &.#{$namespace}-outgoing {
    @include opacity(0.0); }

  &.#{$namespace}-small {
    width: $small;
    padding: $spacing * 0.5;
    margin: $spacing * 0.5; }
  &.#{$namespace}-medium {
    width: $medium;
    padding: $spacing;
    margin: $spacing; }
  &.#{$namespace}-large {
    width: $large;
    padding: $spacing * 1.5;
    margin: $spacing * 1.5; }

  &.#{$namespace}-default {
    color: #FFF;
    background: #7f8c8d; }
  &.#{$namespace}-error {
    color: #FFF;
    background: #C0392B; }
  &.#{$namespace}-notice {
    color: #FFF;
    background: #2ECC71; }
  &.#{$namespace}-warning {
    color: #FFF;
    background: #F39C12; }

  .#{$namespace}-close {
    cursor: pointer;
    float: right;
    font-size: 14px;
    line-height: 18px;
    font-weight: normal;
    font-family: helvetica, verdana, sans-serif; }

  .#{$namespace}-title {
    font-size: 18px;
    line-height: 24px; }

  .#{$namespace}-message {
    font-size: 14px;
    line-height: 18px; } }
